import React from 'react';
import Img from 'gatsby-image';

import { colors } from '../constants';
import Container from './Container';

export default function BlockQuote({
  title,
  author,
  children,
  image,
  noColor = false,
}) {
  return (
    <section className="block-quote">
      <Container>
        <div className="block-quote__wrap">
          {image && (
            <Img
              fluid={image}
              imgStyle={{ objectFit: 'contain' }}
              alt={title}
              className="block-quote__image"
            />
          )}
          <div className="block-quote__quote">{children}</div>
          <div className="block-quote__author">{author}</div>
          <div className="block-quote__title">{title}</div>
        </div>
      </Container>
      <style jsx>{`
        .block-quote {
          ${noColor ? '' : `background-color: ${colors.pinkLight};`}
        }

        .block-quote__wrap {
          padding: 60px 0;
          position: relative;
          text-align: center;
        }

        .block-quote__quote {
          font-size: 32px;
          line-height: 1.25;
          max-width: 680px;
          margin: 0 auto;
          padding: 20px 0;
        }

        .block-quote__author {
          font-size: 21px;
          font-weight: 500;
        }

        .block-quote__title {
          font-size: 20px;
        }
      `}</style>
      <style jsx global>{`
        .block-quote__image {
          max-width: 140px;
          margin: 0 auto;
        }
      `}</style>
    </section>
  );
}
